<template>
  <router-view
    @mouseenter="triggerHover(true)"
    @mouseleave="triggerHover(false)"
  />
</template>

<script>
export default {
  name: "MainLayout",
  setup(props, { emit }) {
    const triggerHover = (status) => {
      emit("hover", status);
    };
    return {
      triggerHover,
    };
  },
};
</script>

<style>
@import "@/assets/css/main.css";
</style>
