<template>
  <ul v-if="menu" class="rightMenu en">
    <li
      v-for="sub in menu"
      :key="sub"
      :class="{ active: $route.path === `/portfolio/${sub.link}` }"
    >
      <router-link
        :to="`/portfolio/${sub.link}`"
        @mouseenter="$emit('mouseenter')"
        @mouseleave="$emit('mouseleave')"
      >
        {{ sub.menu }}
      </router-link>
    </li>
  </ul>
  <div v-else class="rightMenu en careerMenu">
    <p>SM Entertainment</p>
    <em>2019.09. ~ 2023.10.</em>
  </div>
</template>

<script>
export default {
  name: "RightMenu",
  props: {
    menu: Array,
  },
  emits: ["mouseenter", "mouseleave"],
};
</script>
