import { createRouter, createWebHistory } from "vue-router";

import SubLayout from "@/layout/SubLayout.vue";

import MainView from "@/views/MainView.vue";
import EducationView from "@/views/about/EducationView.vue";
import SkillsView from "@/views/about/SkillsView.vue";
import WearingeulView from "@/views/project/WearingeulView.vue";
import JomaloneView from "@/views/project/JomaloneView.vue";
import KwangyaView from "@/views/project/KwangyaView.vue";
import SmEntView from "@/views/career/SmEntView.vue";

const routes = [
  {
    path: "/portfolio/",
    component: MainView,
  },
  {
    path: "/portfolio/education",
    component: EducationView,
    meta: { layout: SubLayout },
  },
  {
    path: "/portfolio/skills",
    component: SkillsView,
    meta: { layout: SubLayout },
  },
  {
    path: "/portfolio/wearingeul",
    component: WearingeulView,
    meta: { layout: SubLayout },
  },
  {
    path: "/portfolio/jomalone",
    component: JomaloneView,
    meta: { layout: SubLayout },
  },
  {
    path: "/portfolio/kwangya119",
    component: KwangyaView,
    meta: { layout: SubLayout },
  },
  {
    path: "/portfolio/career",
    component: SmEntView,
    meta: { layout: SubLayout },
  },
];

export default createRouter({
  history: createWebHistory(),
  routes,
});
