<template>
  <div
    class="mockupImg wearingeul center"
    @click="modalOpen"
    @mouseenter="triggerHover(true)"
    @mouseleave="triggerHover(false)"
  >
    <img src="../../assets/img/wearingeul_mockup.png" />
  </div>
  <detail-modal
    :onClass="onClass"
    projectName="wearingeul"
    capture="글입다 메인홈"
    enName="Wearingeul"
    koName="글입다"
    category="Vue3"
    :percent="100"
    :term="2"
    :project="page"
    link="https://choseran.github.io/wearingeul/"
    @modalClose="modalClose"
    @mouseenter="triggerHover(true)"
    @mouseleave="triggerHover(false)"
  >
    <p>
      Vue3 프로젝트로 컴포넌트와 레이아웃 활용을 위해 리스트 페이지와 상세
      페이지, 그리고 다른 레이아웃인 로그인 페이지와 회원가입 페이지를
      만들었습니다.
    </p>
    <p>배열 형식의 데이터를 js파일로 따로 분리하여 재사용률을 높였습니다.</p>
    <p>
      리스트 페이지에서는 서브 메뉴에 따라 필터링이 되고 그에 따라 페이징도
      반응하도록 구현되어있습니다.
    </p>
  </detail-modal>
  <right-menu
    :menu="submenu"
    @mouseenter="triggerHover(true)"
    @mouseleave="triggerHover(false)"
  />
</template>

<script>
import DetailModal from "@/components/DetailModal.vue";
import RightMenu from "@/components/RightMenu.vue";
export default {
  components: { DetailModal, RightMenu },
  name: "WearingeulView",
  data() {
    return {
      page: [
        "메인 페이지",
        "리스트 페이지 (베스트, 신상품, 할인, 콜라보, 딥펜)",
        "상세 페이지",
        "회원가입 페이지",
        "로그인 페이지",
      ],
      submenu: [
        { link: "wearingeul", menu: "Wearingeul" },
        { link: "jomalone", menu: "Jo Malone" },
        { link: "kwangya119", menu: "Kwangya 119" },
      ],
      onClass: false,
    };
  },
  methods: {
    modalOpen() {
      this.onClass = true;
    },
    modalClose() {
      this.onClass = false;
    },
  },
  setup(props, { emit }) {
    const triggerHover = (status) => {
      emit("hover-trigger", status);
    };
    return {
      triggerHover,
    };
  },
};
</script>
