<template>
  <div
    class="mockupImg jomalone center"
    @click="modalOpen"
    @mouseenter="triggerHover(true)"
    @mouseleave="triggerHover(false)"
  >
    <img src="../../assets/img/jomalone_mockup.png" />
  </div>
  <detail-modal
    :onClass="onClass"
    projectName="jomalone"
    capture="조 말론 메인홈"
    enName="Jo Malone"
    koName="조 말론"
    category="HTML5"
    :percent="100"
    :term="1"
    :project="page"
    link="https://choseran.github.io/jomalone/"
    @modalClose="modalClose"
    @mouseenter="triggerHover(true)"
    @mouseleave="triggerHover(false)"
  >
    <p>
      한 페이지 안에서 여러개의 Swiper를 넣어서 각자 따로 작동이 잘 되도록
      했습니다.
    </p>
    <p>
      배너 Swiper는 반응형으로 PC 사이즈에서는 작동하고 767px 아래로는 멈추도록
      하여 모바일 특성을 살렸습니다.
    </p>
    <p>
      중간에 있는 신상품 Swiper는 기존 PC 사이즈에서는 4개씩 보여주다가
      767px아래로는 1개씩 보이도록 했습니다.
    </p>
  </detail-modal>
  <right-menu
    :menu="submenu"
    @mouseenter="triggerHover(true)"
    @mouseleave="triggerHover(false)"
  />
</template>

<script>
import DetailModal from "@/components/DetailModal.vue";
import RightMenu from "@/components/RightMenu.vue";
export default {
  components: { DetailModal, RightMenu },
  name: "JomaloneView",
  data() {
    return {
      page: ["메인 페이지"],
      submenu: [
        { link: "wearingeul", menu: "Wearingeul" },
        { link: "jomalone", menu: "Jo Malone" },
        { link: "kwangya119", menu: "Kwangya 119" },
      ],
      onClass: false,
    };
  },
  methods: {
    modalOpen() {
      this.onClass = true;
    },
    modalClose() {
      this.onClass = false;
    },
  },
  setup(props, { emit }) {
    const triggerHover = (status) => {
      emit("hover-trigger", status);
    };
    return {
      triggerHover,
    };
  },
};
</script>
