<template>
  <div class="skillContent en center">
    <div class="content" v-for="skill in skills" :key="skill">{{ skill }}</div>
  </div>
  <right-menu
    :menu="submenu"
    @mouseenter="triggerHover(true)"
    @mouseleave="triggerHover(false)"
  />
</template>

<script>
import RightMenu from "@/components/RightMenu.vue";
export default {
  components: { RightMenu },
  name: "SkillsView",
  data() {
    return {
      skills: [
        "HTML5",
        "CSS3",
        "JavaScript",
        "JQuery",
        "Vue3",
        "Tailwind CSS",
        "Figma",
        "Zeplin",
      ],
      submenu: [
        { link: "education", menu: "Education" },
        { link: "skills", menu: "Skills" },
      ],
    };
  },
  setup(props, { emit }) {
    const triggerHover = (status) => {
      emit("hover-trigger", status);
    };
    return {
      triggerHover,
    };
  },
};
</script>
