<template>
  <div class="theBall-outer" :class="{ zoom: isHovered }">
    <div class="theBall"></div>
  </div>
  <div class="wrap">
    <h1 class="topBg">Portfolio</h1>
    <component
      :is="this.$route.meta.layout || 'MainLayout'"
      @hover="addCustomClass"
    />
    <h1 class="bottomBg">Portfolio</h1>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import MainLayout from "./MainLayout.vue";
import SubLayout from "./SubLayout.vue";
export default {
  name: "BaseLayout",
  components: {
    MainLayout,
    SubLayout,
  },
  setup() {
    let isHovered = ref(false);

    let addCustomClass = (status) => {
      isHovered.value = status;
    };

    onMounted(() => {
      const getVendorPerfix = () => {
        const styles = window.getComputedStyle(document.documentElement, "");
        const match = Array.prototype.slice
          .call(styles)
          .join("")
          .match(/-(moz|webkit|ms)-/);
        return match ? `-${match[1]}-` : "";
      };

      const prefix = getVendorPerfix();

      const onMouseMove = (e) => {
        const mouseX = e.pageX + 15;
        const mouseY = e.pageY + 15;

        const ball = document.querySelector(".theBall-outer");
        if (ball) {
          ball.style.transform = `translate(${mouseX}px, ${mouseY}px)`;
        }
      };

      document.addEventListener("mousemove", onMouseMove);

      return {
        prefix,
      };
    });
    return {
      isHovered,
      addCustomClass,
    };
  },
};
</script>
