<template>
  <div class="subContent">
    <left-menu
      @mouseenter="triggerHover(true)"
      @mouseleave="triggerHover(false)"
    />
    <transition name="fade">
      <router-view :key="$route.fullPath" @hover-trigger="triggerHover" />
    </transition>
  </div>
</template>

<script>
import LeftMenu from "@/components/LeftMenu.vue";
export default {
  components: { LeftMenu },
  name: "SubLayout",
  setup(props, { emit }) {
    const triggerHover = (status) => {
      emit("hover", status);
    };
    return {
      triggerHover,
    };
  },
};
</script>

<style>
@import "@/assets/css/main.css";
</style>
