<template>
  <div class="detailModal" :class="{ on: onClass }">
    <span
      class="closeBtn"
      @click="modalClose"
      @mouseenter="$emit('mouseenter')"
      @mouseleave="$emit('mouseleave')"
    >
      <svg
        width="27"
        height="27"
        viewBox="0 0 27 27"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M26.0001 25.9996L1.00049 1"
          stroke="#181818"
          stroke-width="1.5"
          stroke-linecap="round"
          stroke-linejoin="round"
        />
        <path
          d="M1 25.9996L25.9996 1"
          stroke="#181818"
          stroke-width="1.5"
          stroke-linecap="round"
          stroke-linejoin="round"
        />
      </svg>
    </span>
    <div class="detailImg">
      <img
        :src="require(`@/assets/img/${projectName}.png`)"
        :alt="`${capture} 전체 스크린샷`"
      />
    </div>
    <div class="detailText">
      <div class="titleText">
        <h4>{{ enName }}</h4>
        <h4>{{ koName }}</h4>
        <span class="category">{{ category }}</span>
      </div>
      <div class="topText">
        <p>본인 참여도 {{ percent }}%</p>
        <p v-if="term">제작 기간: {{ term }}달</p>
      </div>
      <div class="bottomText">
        <slot></slot>
        <i>구현한 페이지</i>
        <ul>
          <li v-for="page in project" :key="page">{{ page }}</li>
        </ul>
      </div>
      <a
        v-if="link"
        :href="`${link}`"
        target="_blank"
        class="siteBtn"
        @mouseenter="$emit('mouseenter')"
        @mouseleave="$emit('mouseleave')"
        >페이지 보러가기</a
      >
      <p v-else class="attention">현재는 리뉴얼되어 볼 수 없습니다.</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "DetailModal",
  props: {
    onClass: Boolean,
    projectName: String,
    capture: String,
    enName: String,
    koName: String,
    category: String,
    percent: Number,
    term: Number,
    project: Array,
    link: String,
  },
  methods: {
    modalClose() {
      this.$emit("modalClose");
    },
  },
  emits: ["mouseenter", "mouseleave"],
};
</script>
